.Aside{
    border:1px solid black;
    -ms-flex-preferred-size: 15%;
        flex-basis: 15%;
    height: 79vh;
    min-width: 200px;
}

.Aside img{
    width:160px;
}

.addItem{
    margin:5px;
}

.Aside ul {
    text-decoration: none;
    padding: 0;
}

.Aside ul li{
    list-style-type: none;
}

.Aside p{
    text-align: center;
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
}


